import "./menu.scss";
import { Link } from "react-router-dom";


export default function Menu({menuOpen, setMenuOpen}) {
    return (
        <div className={"menu " + (menuOpen && "active")}>
            <ul>
                
                <li onClick={() => setMenuOpen(false)}>
                    <a href="#intro">Home</a>
                </li> 
                
                <li onClick={() => setMenuOpen(false)}>
                    <a href="#games">Games</a>
                </li> 
                
                <li onClick={() => setMenuOpen(false)}>
                    <a href="#arts">Arts</a>
                </li> 
               
                <li onClick={() => setMenuOpen(false)}>
                    <a href="#contact">Contact</a>
                </li> 
                  
                <a
                className="toAbout"
                href="/about"
                target="_blank"
                rel="noreferrer"
            >  About </a>
            </ul>
        </div>
    )
}
