import "./arts.scss";
import { useState } from 'react';

export default function Arts() {
    const [currentSlide, setCurrentSlide] = useState(0);
    const data = [
        
        {
            id:1,
            title: "image2",
            img:"https://free4kwallpapers.com/uploads/wallpaper/voltron-legendary-defender-anime-wallpaper-1024x768-wallpaper.jpg",
        },
        {
            id:2,
            title: "image4",
            img: "https://free4kwallpapers.com/uploads/wallpaper/anime-clouds-1024x768-wallpaper.jpg",
        },
        {
            id:3,
            title: "image5",
            img: "https://free4kwallpapers.com/uploads/wallpaper/awesome-fantasy-world-artwork-anime-hd-wallpaper-1024x768-wallpaper.jpg",
        },


    ];  
    const handleClick =  (way)=>{
        way === "left" ? setCurrentSlide(currentSlide > 0 ? currentSlide-1 : 2)  :
        setCurrentSlide(currentSlide<data.length -1 ? currentSlide+1 : 0)
    }

    return (
        <div className="arts" id="arts">
            <div className="slider" style = {{transform: `translateX(-${currentSlide * 100}vw)` }}>
        {data.map(d=>(  

       
   
             <div className="container">
                    <div className="item">
                        <div className="anh">
                            <img src={d.img} alt=""/>                        
                        </div>
                    </div>
                </div> 
              ))}   
            </div>
        
            <img src="assets/Arrowfixed.png" className="arrow left" alt="" onClick={()=>handleClick("left")}/>
            <img src="assets/Arrowfixed.png" className="arrow right" alt="" onClick={()=>handleClick()}/>
        </div>

    )
}
